/*------------------------------------------------------------------
   -------------------------BODY---------------------------------
-------------------------------------------------------------------*/

body {
  direction: rtl;
  font-family: 'PT Sans', sans-serif;
  font-size: 14px;
  height: 100%;
  background: #f9f9f9;
}

body.single-page {
  background: #565656;
  direction: rtl;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Armata', sans-serif;
}

ul.mainNav li a {
  font-family: 'Armata', sans-serif;
}

h1 {
  font-size: 24px;
}

h2 {
  font-size: 22px;
}

h3 {
  font-size: 20px;
}

h4 {
  font-size: 18px;
}

h5 {
  font-size: 16px;
}

h6 {
  font-size: 14px;
}

p {
  line-height: 23px;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px dashed #e0e0e0;
  margin: 1em 0;
  padding: 0;
}

pre {
  border-radius: 1px;
  -moz-border-radius: 1px;
  -webkit-border-radius: 1px;
}

label {
  font-weight: 400;
}

text {
  font-weight: normal;
}

/*-----------------------------------------------------------------*/

/*------------------------------------------------------------------
   -------------------------HEADER---------------------------------
-------------------------------------------------------------------*/

.ls-header {
  font-size: 15px;
}

.ls-top-header {
  margin-bottom: 15px;
  margin-top: 5px;
}

nav {
  background: #555557;
}

.header-logo {
  text-align: center;
  width: 180px;
  float: right;
}

.header-logo a {
  line-height: 50px;
  color: #2f99e8;
}

.header-logo a span {
  color: #2f99e8;
}

.header-logo a:hover {
  text-decoration: none;
}

#main-container {
  width: 100%;
  height: 100%;
  background: #ffffff;
  color: #565656;
}

/*-----------------------------------------------------------------*/

/*------------------------------------------------------------------
  -------------------------- NAVIGATION -------------------------
-------------------------------------------------------------------*/

/*** Top Navigation ***/

.menu-control {
  float: right;
  line-height: 38px;
  margin-right: 5px;
  text-align: center;
}

.menu-control a {
  color: #ffffff;
  outline: none;
}

.menu-control a:hover {
  color: #FF7878;
}

.menu-control a i {
  line-height: 25px;
  font-size: 18px;
}

.top-navigation ul {
  list-style: none;
  padding: 0;
  margin: 0;
  float: left;
}

.top-navigation ul li {
  display: inline-block;
}

.top-navigation ul li a {
  color: #ffffff;
  text-decoration: none;
  line-height: 17px;
  outline: none;
  padding: 10px 15px;
  display: inline-block;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  font-size: 18px;
}

.top-navigation ul li a:hover {
  background: #7a8b83;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.top-navigation ul li a.right-sidebar,
.top-navigation ul li a.right-sidebar-setting {
  -webkit-transition: none 0.3s ease-in-out;
  -moz-transition: none 0.3s ease-in-out;
  -o-transition: none 0.3s ease-in-out;
  transition: none 0.3s ease-in-out;
}

.top-navigation ul li.only-phone {
  display: none;
}

.top-navigation ul li.only-phone a {
  -webkit-transition: none 0.3s ease-in-out;
  -moz-transition: none 0.3s ease-in-out;
  -o-transition: none 0.3s ease-in-out;
  transition: none 0.3s ease-in-out;
}

.top-navigation ul li.only-phone a:hover {
  background: none;
  color: #FF7878;
  -webkit-transition: none 0.3s ease-in-out;
  -moz-transition: none 0.3s ease-in-out;
  -o-transition: none 0.3s ease-in-out;
  transition: none 0.3s ease-in-out;
}

.search-box {
  float: right;
  margin-right: 5px;
  text-align: center;
}

.search-box ul li a span {
  font-size: 18px;
  line-height: 12px;
}

.search-box input[type=search] {
  background: url("./assets/images/b.png") no-repeat scroll 9px center / 19px 19px #f2f2f2;
  border: none;
  color: #000;
  cursor: auto;
  height: 40px;
  outline: 0;
  padding-right: 32px;
  width: 320px;
  -webkit-appearance: textfield;
  -webkit-box-sizing: content-box;
  font-family: inherit;
  font-size: 100%;
}

.search-box input::-webkit-input-placeholder {
  color: #999;
}

.search-box input::-webkit-search-decoration,
.search-box input::-webkit-search-cancel-button {
  display: none;
}

/*Top dropDown navigation Start*/

.top-navigation li.open:after {
  border-bottom: 6px solid #FF7878;
  border-right: 6px solid rgba(0, 0, 0, 0);
  border-left: 6px solid rgba(0, 0, 0, 0);
  content: "";
  display: inline-block;
  position: absolute;
  left: 18px;
  top: 33px;
  z-index: 1;
}

.red-color .top-navigation li.open:after {
  border-bottom: 6px solid #C45C5C;
}

.top-navigation li a span.badge {
  /**** Issue Fixed @new ***/
  font-size: 10px;
  font-weight: normal;
  padding: 0;
  position: absolute;
  left: 5px;
  text-align: center;
  top: 4px;
  font-family: 'Pontano Sans', sans-serif;
  height: 15px;
  width: 15px;
  line-height: 15px;
  display: block;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
}

.top-navigation .dropdown-menu.right {
  right: auto;
  left: 0;
}

.top-navigation .dropdown-menu {
  margin: 0;
  border-radius: 1px;
  -moz-border-radius: 1px;
  -webkit-border-radius: 1px;
  padding: 0;
  border: none;
  min-width: 275px;
}

.top-navigation .dropdown-menu h4 {
  padding: 10px;
  margin: 0;
  font-size: 14px;
  color: white;
  background: #ff7878;
}

.red-color .top-navigation h4 {
  background: #C45C5C;
}

.top-navigation .dropdown-menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
  float: none;
}

.top-navigation .dropdown-menu ul li.only-link {
  text-align: center;
  padding: 5px 0;
  border-bottom: none;
  margin-top: 0;
}

.top-navigation .dropdown-menu ul li.only-link a {
  font-size: 14px;
}

.ls-feed {
  list-style: none;
  padding: 0;
  margin: 0;
}

.ls-feed li {
  background-color: #e0e0e0;
  font-size: 14px;
  line-height: 14px;
  margin-bottom: 10px;
  padding: 10px 33px 10px 0;
  position: relative;
}

.top-navigation ul.ls-feed li {
  display: block;
}

.top-navigation ul.ls-feed li a {
  padding: 0;
  color: #6e6e6e;
  display: block;
  line-height: 22px;
  font-size: 14px;
}

.top-navigation ul.ls-feed li a:hover {
  background: none;
}

.ls-feed li .label {
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  right: 7px;
  line-height: 11px;
  padding: 5px;
  position: absolute;
  top: 11px;
  height: 20px;
  width: 20px;
}

.feed-box .ls-feed li .label {
  top: 7px;
  padding: 5px 0;
}

.ls-feed li .date {
  padding-left: 10px;
  text-align: left;
  font-style: italic;
  font-size: 12px;
  float: left;
  direction: ltr;
}

.dropdown-menu.top-notification h4 {
  color: white;
  background: #ff7878;
}

.red-color .dropdown-menu.top-notification h4 {
  background: #C45C5C;
}

.dropdown-menu.top-notification .ls-feed li {
  margin-bottom: 0;
  border-bottom: 1px dotted #cbc5ba;
  background: white;
  color: dimgrey;
}

/*Top dropDown navigation end*/

/*** Left Navigation ***/

#left-navigation {
  width: 200px;
  background: #58595B;
  float: right;
  position: absolute;
  right: 0;
  height: 100%;
  overflow: visible !important;
}

#left-navigation.active {
  width: 50px;
}

#left-navigation .user-image {
  margin: 10px auto;
  width: 80px;
  position: relative;
}

#left-navigation.active .user-image {
  width: 40px;
}

#left-navigation .user-image img {
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  overflow: hidden;
}

#left-navigation.active .user-image {
  width: 40px;
}

#left-navigation.active .user-image img {
  width: 40px;
}

#left-navigation .user-online-status {
  display: block;
  right: 0;
  position: absolute;
  top: 10px;
}

#left-navigation.active .user-online-status {
  top: -5px;
}

#left-navigation.active .social-icon {
  display: none;
}

#left-navigation .social-icon {
  list-style: none;
  margin: 0 0 5px;
  padding: 0;
  text-align: center;
}

#left-navigation .social-icon li {
  display: inline-block;
  margin: 0 5px;
}

#left-navigation .social-icon li a {
  color: #FF7878;
  font-size: 15px;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

#left-navigation .social-icon li a:hover {
  color: #FFFFFF;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.black-color #left-navigation {
  background: #444755;
}

.blue-color #left-navigation {
  background: #53A2B8;
}

.deep-blue-color #left-navigation {
  background: #324E59;
}

.red-color #left-navigation {
  background: #DD7878;
}

.light-green-color #left-navigation .social-icon li a {
  color: #FFFFFF;
}

.light-green-color #left-navigation .social-icon li a:hover {
  color: #565656;
}

.blue-color #left-navigation .social-icon li a {
  color: #FFFFFF;
}

.blue-color #left-navigation .social-icon li a:hover {
  color: #FF7878;
}

.red-color #left-navigation .social-icon li a {
  color: #FFFFFF;
}

.red-color #left-navigation .social-icon li a:hover {
  color: #3F414D;
}

.light-green-color #left-navigation {
  background: #4FADA8;
}

