/*------------------------------------------------------------------
  -------------------------- BREADCRUMB -------------------------
-------------------------------------------------------------------*/

.ls-breadcrumbs {
  display: inline-block;
  overflow: hidden;
  border-radius: 1px;
}

.ls-breadcrumbs a {
  text-decoration: none;
  outline: none;
  display: block;
  float: right;
  font-size: 12px;
  line-height: 36px;
  color: #565656;
  padding: 0 30px 0 20px;
  background: #E3DFD8;
  position: relative;
}

.ls-breadcrumbs a:first-child {
  padding-right: 20px;
  border-radius: 1px;
}

.ls-breadcrumbs a:first-child:before {
  right: 14px;
}

.ls-breadcrumbs a:last-child {
  border-radius: 1px;
  padding-left: 20px;
}

.ls-breadcrumbs a.active,
.ls-breadcrumbs a:hover {
  background: #e0e0e0;
}

.ls-breadcrumbs a.active:after,
.ls-breadcrumbs a:hover:after {
  background: #e0e0e0;
}

.ls-breadcrumbs a:after {
  content: '';
  position: absolute;
  top: 0;
  left: -18px;
  width: 36px;
  height: 36px;
  transform: scale(0.707) rotate(45deg);
  z-index: 1;
  background: #E3DFD8;
  box-shadow: 2px -2px 0 2px rgba(0, 0, 0, 0.1),
    3px -3px 0 2px #e0e0e0;
  border-radius: 5px 0 50px 0;
}

.ls-breadcrumbs a:last-child:after {
  content: none;
}

.ls-breadcrumbs a .fa {
  font-size: 14px;
  margin-left: 3px;
  color: #565656;
}

.breadcrumb-box {
  margin-bottom: 20px;
}

.breadcrumb li {
  font-size: 12px;
}

.breadcrumb li a {
  color: #565656;
  font-size: 12px;
  outline: 0;
}

.breadcrumb > li + li:before {
  color: #565656;
}

/**** breadcrumb simple ***/

/****** Breadcrumb ****/

.ls-breadcrumbs-simple {
  display: inline-block;
  overflow: hidden;
  border-radius: 1px;
}

.ls-breadcrumbs-simple a {
  text-decoration: none;
  outline: none;
  display: block;
  float: right;
  font-size: 12px;
  color: #565656;
  padding: 0 10px 0 10px;
  position: relative;
}

.ls-breadcrumbs-simple a:first-child {
  padding-right: 0;
  margin-right: 0;
  border-radius: 1px;
}

.ls-breadcrumbs-simple a:first-child:before {
  right: 14px;
}

.ls-breadcrumbs-simple a:last-child {
  border-radius: 1px;
  padding-left: 20px;
}

.ls-breadcrumbs-simple a.active,
.ls-breadcrumbs-simple a.active .fa,
.ls-breadcrumbs-simple a:hover {
  color: $links-hover;
}

.ls-breadcrumbs-simple a.active:after,
.ls-breadcrumbs-simple a:hover:after,
.ls-breadcrumbs-simple a:hover .fa {
  color: $links-hover;
}

.ls-breadcrumbs-simple a:after {
  content: '';
  position: absolute;
  top: 4px;
  left: 0;
  width: 10px;
  height: 10px;
  transform: scale(0.707) rotate(45deg);
  z-index: 1;
  box-shadow: 1px -1px 0 1px rgba(0, 0, 0, 0.1),
    1px -1px 0 1px #E3DFD8;
  border-radius: 5px 0 50px 0;
}

.ls-breadcrumbs-simple a:last-child:after {
  content: none;
}

.ls-breadcrumbs-simple a .fa {
  font-size: 14px;
  margin-left: 3px;
  color: #565656;
}

