  padding: 0;
  list-style: none;
  position: relative;
}

/* The line */

.ls_tmtimeline:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 10px;
  background: #dbcdad;
  right: 25%;
  margin-right: -10px;
}

.ls_tmtimeline > li {
  position: relative;
}

.ls_tmtimeline > li .ls-timeline-user {
  position: absolute;
  text-align: center;
  width: 25%;
  padding-left: 60px;
}

.ls-timeline-user .media-heading {
  margin-top: 15px;
  margin-bottom: 10px;
  color: #5c5b56;
  font-size: 15px;
}

.ls_tmtimeline > li .ls-timeline-user img {
  width: 75px;
}

/* The date/time */

.ls_tmtimeline > li .ls_tmtime {
  display: block;
}

.ls_tmtimeline > li .ls_tmtime span {
  display: block;
}

.ls_tmtimeline > li .ls_tmtime span:first-child {
  font-size: 15px;
  color: #706a5c;
}

.ls_tmtimeline > li .ls_tmtime span:last-child {
  font-size: 15px;
  color: #71624e;
  font-weight: bold;
}

.ls_tmtimeline > li:nth-child(odd) .ls_tmtime span:last-child {
  color: #71624e;
}

/* Right content */

.ls_tmtimeline > li .ls_tmlabel {
  margin: 0 30% 15px 0;
  padding: 10px 15px;
  font-size: 14px;
  font-weight: 300;
  line-height: 20px;
  position: relative;
  border-radius: 1px;
}

.ls_tmtimeline > li:nth-child(odd) .ls_tmlabel {
  background: #5c5b56;
  color: #fff;
}

.ls_tmtimeline > li:nth-child(even) .ls_tmlabel {
  background: #CAB38B;
}

.ls_tmtimeline > li:nth-child(odd) .ls_tmlabel p {
  color: #D1D0CE;
}

.ls_tmtimeline > li .ls_tmlabel h2 {
  margin-top: 0px;
  padding: 0 0 10px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  font-size: 15px;
}

/* The triangle */

.ls_tmtimeline > li .ls_tmlabel:after {
  left: 100%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-left-color: #CAB38B;
  border-width: 10px;
  top: 20px;
}

.ls_tmtimeline > li:nth-child(odd) .ls_tmlabel:after {
  border-left-color: #5c5b56;
}

.ls_tmlabel ul.task {
  padding: 0;
  margin: 0;
}

.ls_tmlabel ul.task li {
  display: block;
  list-style: none;
  text-align: right;
  color: #D1D0CE;
}

.ls_tmlabel ul.task li a {
  color: #D1D0CE;
}

.ls_tmlabel ul {
  padding: 0;
  margin: 0;
  text-align: center;
}

.ls_tmlabel ul li {
  position: relative;
  display: inline-block;
  list-style: none;
  margin: 3px;
}

.ls_tmlabel ul li img {
  width: 90px;
}

/* The icons */

.ls_tmtimeline > li .ls_tmicon {
  width: 40px;
  height: 40px;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  font-size: 1.4em;
  line-height: 40px;
  -webkit-font-smoothing: antialiased;
  position: absolute;
  color: #806854;
  background: #fff5cf;
  border-radius: 50%;
  box-shadow: 0 0 0 8px #b39c7d;
  text-align: center;
  right: 25%;
  top: 0;
  margin: 7px -25px 0 0;
}

.ls_tmtimeline > li.month {
  position: relative;
